/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"student-vision\">\n   <div class=\"breadcrum position-relative float-left w-100 mb-5\">\n      <h3 class=\"title-txt text-center w-100 float-left text-white\">Chairman's Desk</h3>\n   </div>\n   <div class=\"container mb-5\">\n      <div class=\"row\">\n         <div class=\"col-12 text-center mb-3 logo-wrapper\">\n            <img src=\"./assets/images/desk/chairmans-desk.jpg\" style=\"width:300px\" class=\"mb-3\" alt=\"Chairman's Desk\">\n            <h4>Dr. Dinesh Pratapwar</h4>\n         </div>\n         <div class=\"col-12 text-center\">\n            <div class=\"about-desc\">\n               <p class=\"mb-4\">An exuberant welcome to the world of Saptagiri Podar Learn School, Degloor, where education means all round development of the child. This is a school that has a mission to make each child achieve his and her best.</p>\n               <p class=\"mb-4\">“Education awakens the power and beauty that lie within us.” Education does not only mean academic excellence. It rather is a harmonious and synchronized combination of hand (skills like various arts), head (Intellectual Power) and heart (Value System). In the present era of digitalized world, it the biggest challenge before educators and parents, to nurture the young minds with the indelible impressions of a holistic education.</p>\n               <p class=\"mb-4\">Saptagiri Podar Learn School at Degloor affirms that education begins at birth and continues through life. Our complete focus is to provide a support center to our children so they continue learning and developing towards becoming whole and healthy individuals.</p>\n               <p>We work with the core belief that perception can be cultivated gradually amongst all individuals. It is not enough to make children literate and academically intelligent what is more important is to make them intelligent so that they are able to use their intellect judiciously.</p>\n               <p class=\"mb-4\">We believe that true knowledge is the ability to listen to your heart and that each child's self esteem, dignity, physical and emotional well being must be cultivated with the same magnitude as that has given to academic and material success. The educational needs of today are very different from those of the past because of growing competition and with the progress of science and technology. The students must be equipped with the skills required to face the challenges of future. They can do it easily if we can make the surrounding stimulating and purposeful.</p>\n               <p class=\"mb-4\">Saptagiri Podar Learn school, Degloor therefore has undertaken a great leap in integrating information and communication technology into the class room teaching and learning process.</p>\n               <p class=\"mb-4\">Our school is constantly scaling new heights with excellent professionals training our children in a range of co-curricular activities so that they evolve as excellent individuals who have sharpened their skills in the microcosm of the school and are equipped to lead and inspire in the microcosm of the world. </p>\n               <p class=\"mb-4\">I take this opportunity to thank all the parents who have reposed their faith in our school. We assure to do our very best always.</p>\n               <p class=\"mb-4\">The school always tries its best to maintain the highest quality academic standard and provide the wonderful environment for studies. I would like to end with the words of Albert Einstein:\n                  “Imagination is more important than knowledge. For while knowledge defines all we currently know and understand, imagination points to all us might yet discover and create.”\n                  </p>\n               <p class=\"text-right mb-0 \">Best wishes,</p>\n               <p class=\"text-right mb-0\">Dr. Dinesh Pratapwar</p>\n               <p class=\"text-right font-weight-bold\">SAPTAGIRI PODAR LEARN SCHOOL DEGLOOR</p>\n            </div>\n         </div>\n      </div>\n   </div>\n</div>");

