/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"student-vision\">\n   <div class=\"breadcrum position-relative float-left w-100 mb-5\">\n      <h3 class=\"title-txt text-center w-100 float-left text-white\">Why Us</h3>\n   </div>\n   <div class=\"container mb-5\">\n      <div class=\"row\">\n         <div class=\"col-12 text-center mb-3 logo-wrapper\">\n            <a [routerLink]=\"['/home']\">\n               <img src=\"./assets/images/saptgiri-logo.png\" alt=\"History Image\">\n            </a>\n         </div>\n         <div class=\"col-12 text-center\">\n            \n            <div class=\"about-desc\">\n               <p class=\"mt-4 mb-4\">The Podar System of Education values the child as a strong, capable and resilient individual, rich with wonder and knowledge. Every child brings with him deep curiosity and potential and this innate curiosity drives his interest to understand the world and his place within it.</p>\n               <p class=\"mt-4 mb-4\">Our curriculum allows the child to reflect, solve, create, grow and most importantly think. The core elements that form the gamut of PIC Curriculum are as follows:</p>\n               \n               <ul class=\"with-icon mt-5\">\n                   <li>All subjects are dealt with a Scientific Approach or the Inquiry Approach that uses engaging visuals and presentation formats with well-crafted activities to arouse the learner's procedural, manipulative, observational, drawing, reporting and interpretative skills. This motivates learners to learn, enabling them to better grasp the key concepts of every subject. At the same time, emphasis is given on developing learner's inquiry and process skills.</li>\n                   <li>Problem Solving Skills are drilled by involving learners in identifying problems, applying previous knowledge to solve problems, reasoning and formulating conclusions. Learners develop a basic ability to solve problems and apply their learning in real life and new situations. They develop deeper understanding of concepts by solving problems.</li>\n                   <li>Profound thinking requires both imagination and intellectual ideas. To produce excellence in thinking we engage our learners in a curriculum that overlaps the logical and the imaginative. Creative thinking is correlated with scientific creativity and is cultivated through building concept maps, sequencing events, slogan writing, and imaginary/creative writing on hypothetical situations. Our curriculum provides students with ample opportunities to practice higher- order thinking.</li>\n               </ul>\n            </div>\n         </div>\n      </div>\n   </div>\n</div>");

